$rad: 1.25rem;
$top-gap: 0rem;

.container {
  height: 100%;
  border-radius: $rad;
  position: relative;
  overflow: hidden;
  padding-top: 1rem;
}

.background_image {
  height: 100%;
  width: 100%;
  display: flex;
  border-radius: $rad;
  justify-content: center;
  align-items: center;
}

.no_image {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 1rem;
}

.info {
  max-height: calc(100% - $top-gap - 1rem);
  height: fit-content;
  width: 100%;
  border-radius: $rad;
  background-color: var(--constant-dark-color-80);
  position: absolute;
  bottom: 0;
  transition: height 0.5s ease;
}

.badges_container {
  display: flex;
  column-gap: 0.5rem;
  position: absolute;
  right: 1rem;
  top: -1rem;
}

.header {
  display: flex;
  align-items: last baseline;
  padding: 1rem 1rem;
  padding-bottom: var(--header-padding);
  transition: padding-bottom .5s ease-in;
  &.expanded{
    padding-bottom: 1rem;
  }
}

.header_general {
  display: flex;
  flex-direction: column;
  gap: .25rem;
  flex: 1;
}

.description {
  width: 100%;
  border-radius: $rad;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
  height: 0px;
  transition: height .5s linear;
  &.expanded {
    height: 80vh;
  }
  &__content{
    padding: 1rem;
  }
}

.image {
  width: 100%;
  height: 100%;
  border-radius: calc($rad + 3px);
  object-fit: cover;
  object-position: center;
}

.icon__swipe {
  color: var(--constant-light-color);
}

.photoSlider {
  position: absolute;
  top: 2rem;
  left: 1rem;
  right: 1rem;
  display: flex;
  gap: .5rem;

  &__point {
    width: 100%;
    height: .25rem;
    border-radius: 1rem;
    background-color: var(--constant-light-color);
    opacity: .5;
    &_active {
      opacity: 1;
    }
  }
}
